import base from '@/services/servicio-base'

const servicio = {}


servicio.guardarValoracion = (datos) => {
    return base.post('/correos/guardarValoracion',{datos})
    .then(r=>{
            return r.data;
        }).catch(e=>{
            return e ;
    });
}

servicio.verificarEncuesta = (codPedido) => {
    return base.post('/correos/verificarEncuesta',{codPedido})
    .then(r=>{
            return r.data;
        }).catch(e=>{
            return e ;
    });
}
export default servicio