<template>
  <div class="container-fluid color">
    <div class="row">
      <div class="col-12 text-right color-fondo">
        <img v-bind:src="require('@/assets/C2H.png')" height="80px" />
      </div>
    </div>

    <div class="mt-3">
      <h2 class="text-center">¡ GRACIAS POR TU PREFERENCIA !</h2>
    </div>

    <div class="d-flex justify-content-center my-4" v-if="encuestaResuelta">
      <h1>LA ENCUESTA YA FUE PREVIAMENTE COMPLETADA</h1>
    </div>

    <div v-if="!encuestaResuelta">
      <div class="row mt-4">
        <div class="col-12">
          <h5 for="encabezado" class="text-center">
            ENCUESTA DE SATISFACCIÓN DEL CLIENTE
          </h5>
        </div>
      </div>

      <div class="row mt-4">
        <div class="col-12">
          <p for="msg" style="font-size: 20px" class="text-justify">
            Fue un placer para nosotros Concreto2H el haberle prestado de sus servicios,
            estamos en constante crecimiento es por ello que su opinión es de suma
            importancia.
          </p>
        </div>
      </div>
      <div class="row">
        <div class="col-12">
          <label for="" class="textoEncabezado">
            En general, ¿qué grado de satisfacción tiene hacia del servicio?</label
          >
          <vue-feedback-reaction
            v-model="feedbackGral"
            :labels="['Muy mal', 'Mal', 'Normal', 'Bueno', 'Excelente']"
            style="font-size: 19"
          />
        </div>
      </div>
      <div class="row">
        <div class="col-12">
          <label for="" class="textoEncabezado">
            ¿Cómo valora la atención recibida por el Personal ?</label
          >
          <vue-feedback-reaction
            v-model="feedbackPersonal"
            :labels="['Muy mal', 'Mal', 'Normal', 'Bueno', 'Excelente']"
          />
        </div>
      </div>

      <div class="row">
        <div class="col-12">
          <label for="" class="textoEncabezado">
            ¿Que opinión le merece el cumplimiento de los plazos de entrega del
            producto?</label
          >
          <vue-feedback-reaction
            v-model="feedbackPlazo"
            :labels="['Muy mal', 'Mal', 'Normal', 'Bueno', 'Excelente']"
            label-class="font-medium text-sm text-gray-500"
          />
        </div>
      </div>

      <div class="row mt-5">
        <div class="col-12">
          <label for="opinion" class="textoEncabezado">Dejenos sus observaciones: </label>
        </div>
        <div class="col-12">
          <textarea class="form-control" v-model="comentarios"></textarea>
        </div>
      </div>

      <div class="row mt-3">
        <div class="col-12 mb-2">
          <button class="btn btn-primary col-12" @click="enviar">Enviar</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import servicio from "@/services/servicio-encuesta";

export default {
  data() {
    return {
      encuestaResuelta: false,
      feedbackGral: "",
      feedbackPersonal: "",
      feedbackPlazo: "",
      comentarios: "",
      codPedido: 0,
    };
  },
  created() {
    this.codPedido = this.$route.params.id != undefined ? this.$route.params.id : 0;
  },
  mounted() {
    this.varificarEncuestaValoracion();
  },
  methods: {
    enviar() {
      let datos = {
        codPedido: this.codPedido,
        general: this.feedbackGral,
        personal: this.feedbackPersonal,
        producto: this.feedbackPlazo,
        comentarios: this.comentarios,
      };

      this.$loading(true);
      servicio
        .guardarValoracion(datos)
        .then(() => {
          this.varificarEncuestaValoracion();
          this.$loading(false);
        })
        .catch((error) => {
          this.$loading(false);
          console.error(error);
        });
    },
    varificarEncuestaValoracion() {
      this.$loading(true);
      servicio
        .verificarEncuesta(this.codPedido)
        .then((result) => {
          if (result.value) {
            this.encuestaResuelta = false;
          } else {
            this.encuestaResuelta = true;
          }

          this.$loading(false);
        })
        .catch((error) => {
          this.$loading(false);
          console.error(error);
        });
    },
  },
};
</script>

<style scoped>
.textoEncabezado {
  font-size: 18;
  font-weight: bold;
}

.color-fondo {
  background-color: #3a93ff;
  border-style: solid;
  border-color: white;
}

.color {
  background-color: white;
}
</style>
